.Name {
  font-size: 60px;
  text-align: center;
  margin-bottom: 0;
  padding: 4px;
  padding-left: 2px;
  padding-right: 2px;
}

.profilePic {
  border: 2px black solid;
  width: 200px;
  margin: 20px;
  margin-right: 0px;
  border-radius: 50%;
}

.aboutDesc {
  text-align: left;
  margin: 0px;
  margin-left: 0px !important;
  min-width: 200px;
}

.blogPosts {
  display: block;
  margin-right: auto;
  margin-left: auto;
  min-width: 300px;
  width: 70%;
}

.flexContain {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}

.desc {
  margin: 10px;
}

.post {
  font-size: 20px;
}

.aboutMe {
  font-size: 20px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.posts {
  display: flex;
  flex-direction: column;
}

.flexBlog {
  display: flex;
  align-content: center;
  justify-content: space-around;
  width: 100%;
  flex-wrap: wrap;
}

.blogOrProject {
  width: 600px;
  max-width: 95%;
}

.blog {
  margin: 20px;
  background-color: green;
  padding-right: 15px;
}

::selection {
  color: purple;
  background: yellow; /* WebKit/Blink Browsers */
}
::-moz-selection {
  color: purple;
  background: yellow; /* WebKit/Blink Browsers */
}

.highlight {
  font-size: 20px;
  color: purple;
  background: yellow;
  padding: 10px;
}

.App {
  font-family: "Poppins", sans-serif;
}

.longerDesc {
  margin: 20px;
  padding-right: 30px;
  max-width: 500px;
}

.Title {
  font-family: "Poppins", sans-serif;
  margin-top: -13px;
  padding: 4px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 20px;
}

.titleContainer {
  margin-left: 1%;
  width: 99%;
  margin: 0px;
  animation: wiggleText 2s infinite ease-in-out alternate;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 0;
  transition: background-color 0.2s;
}

.socialMail {
  color: black;
  margin: 20px;
  padding: 10px;
  transition: background-color 0.2s;
  border-radius: 45%;
}

.socialTwitter {
  color: black;
  margin: 20px;
  padding: 10px;
  transition: color 0.2s;
  border-radius: 45%;
}

.socialGit {
  color: black;
  margin: 20px;
  padding: 10px;
  transition: color 0.2s;
  border-radius: 45%;
}

.socialContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none;
}

.titleContainer:hover .Name {
  background-color: yellow;
  color: purple;
}

.titleContainer:hover .Title {
  background-color: yellow;
  color: purple;
}

.socialTwitter:hover {
  border-radius: 50%;
  color: rgb(0, 47, 255);
  animation: spin 0.4s ease-in-out;
}

.socialMail:hover {
  border-radius: 50%;
  color: rgb(0, 195, 255);
  animation: spin 0.4s ease-in-out;
}

.socialGit:hover {
  border-radius: 50%;
  color: rgb(140, 0, 255);
  animation: spin 0.4s ease-in-out;
}

@keyframes wiggleText {
  from {
    transform: rotateZ(1deg);
  }
  to {
    transform: rotateZ(-1deg);
  }
}

@keyframes spin {
  from {
    transform: rotateZ(0deg);
  }
  to {
    transform: rotateZ(360deg);
  }
}
